import React, { useState } from 'react';
import axios from 'axios';

import { base_url, alert_toast, get_color_set } from './helper';
import Table from './helper/TableExternalData';
import MiniChart from 'react-mini-chart';

export default function Page(props) {
  const [key_for_reset, resetTable] = useState(0);

  const colors = get_color_set(8);
  const propsMini = {
    activePointRadius: 3,
    strokeWidth: 2,
    labelFontSize: 12,
    width: 180,
    height: 35,
  }
  return (
    <>
      <section className="main-wrapper">
        <div className="container-fluid pt-3">
          <div className="card">
            <div className="card-header">
              <div className="card-title">List of employee</div>
              <div className="card-tools">
                <button className="btn btn-sm btn-danger"><i className="far fa-file-pdf" /> export to pdf</ button>
              </div>
            </div>
            <div className="card-body p-0 ">
              <Table data={[
                {
                  name: "Yuma Yusuf",
                  eid: "12413141314",
                  curr_app: "Firefox - Youtube",
                  cpi: "45.5",
                  status: "logging",
                },
                {
                  name: "Christa agung",
                  eid: "12413141314",
                  curr_app: "Ficrosoft Excel",
                  cpi: "45.5",
                  status: "shutdown",
                },
              ]} columns={
                [{
                  name: 'name',
                  selector: 'name'
                }, {
                  name: 'EID',
                  selector: 'eid'
                }, {
                  name: 'Current Apps',
                  selector: 'curr_app'
                }, {
                  name: 'Performance index',
                  selector: 'cpi',
                  width: '25%'
                }, {
                  name: 'Status',
                  selector: 'status',
                  width: '10%',
                  cell: (row) => <span className={`badge badge-${(row.status == 'logging' ? "primary" : "secondary")}`}>{row.status}</span>
                }, {
                  name: 'Action',
                  button: true,
                  cell: (row) => <button className="btn btn-sm btn-primary"><i className="fas fa-receipt" /> detail</button>,
                }]} />
            </div>
          </div>
        </div>
      </section>
    </>
  );
} 